.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;

    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 1s, opacity 0.5s linear;
}

.popup:hover {
    cursor: pointer;
}